.loadding {
  display: none;
}
.pyg_header {
  padding: 5px;
  width: 100%;
  height: 45px;
  position: fixed;
  background-color: #fff;
  top: 0;
  left: 0;
}
.pyg_footer {
  width: 100%;
  height: 45px;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-around;
}
.pyg_footer a {
  padding: 5px;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #000;
}
.pyg_footer a i {
  font-size: 20px;
}
.pyg_footer a div {
  font-size: 14px;
}
.pyg_view {
  overflow: scroll;
  background-color: #eee;
}
html,
body,
.pyg_view {
  height: 100%;
}
body {
  padding-top: 45px;
  padding-bottom: 45px;
}
* {
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}
.loadding::before {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 0.5;
  content: "";
  z-index: 100;
}
.loadding::after {
  font: normal normal normal 14px/1 FontAwesome;
  position: fixed;
  top: 40%;
  left: 50%;
  margin-left: -50px;
  font-size: 100px;
  color: #0094ff;
  content: "\f013";
  z-index: 101;
  animation: fa-spin 1s infinite linear;
}
.pyg_cates {
  display: flex;
  text-align: center;
}
.pyg_cates a {
  flex: 1;
}
.pyg_cates a img {
  width: 80%;
}
.pyg_items .pyg_item > img {
  width: 100%;
}
.pyg_items .pyg_item > ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.pyg_items .pyg_item > ul li {
  width: 49%;
  padding: 5px;
  background-color: #fff;
  margin-bottom: 5px;
}
.pyg_items .pyg_item > ul li a:nth-of-type(1) {
  width: 100%;
}
.pyg_items .pyg_item > ul li a:nth-of-type(1) img {
  width: 100%;
}
.pyg_items .pyg_item > ul li a:nth-of-type(1) div:nth-of-type(1) {
  color: #000;
  height: 40px;
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.pyg_items .pyg_item > ul li a:nth-of-type(1) div:nth-of-type(2) {
  color: #f00;
}
.pyg_items .pyg_item > ul li a:nth-of-type(2) {
  width: 60%;
  display: block;
  margin: auto;
}
